import { ActionContext } from "vuex";
import { user, resp, authState } from "@/shared/types";

export default {
  namespaced: true,
  actions: {
    authRequest(context: ActionContext<authState, authState>, user: user) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          context.commit("authRequest");
          localStorage.setItem("user-token", "token");
          context.commit("authSuccess", "token");
          resolve("done");
        }, 1000);
      });
    },

    authLogout(context: ActionContext<authState, authState>) {
      return new Promise((resolve, reject) => {
        context.commit("authLogout");
        localStorage.removeItem("user-token"); // clear your user's token from localstorage
        resolve("out");
      });
    },
  },
  mutations: {
    authRequest(state: authState) {
      state.status = "loading";
    },
    authSuccess(state: authState, resp: resp) {
      state.status = "success";
      state.token = resp.token;
      state.hasLoadedOnce = true;
    },
    authError(state: authState) {
      state.status = "error";
      state.hasLoadedOnce = true;
    },
    authLogout(state: authState) {
      state.token = "";
    },
  },
  state: () => ({
    token: localStorage.getItem("user-token") || "",
    status: "",
    hasLoadedOnce: false,
  }),
  getters: {
    isAuthenticated: (state: authState) => !!state.token,
    authStatus: (state: authState) => state.status,
  },
};
